.about-me-container {
    background: rgba(36, 40, 56, 0.92);
    border-radius: 16px;
    border: 2px solid #6dd5ed;
    padding: 2rem;
    box-shadow: 0 2px 16px #6dd5ed40;
    transition: border 0.3s;
}
.about-me-container:hover {
    border-color: #b366f6;
    box-shadow: 0 6px 32px #b366f6a0;
}

.about-me-container p {
    font-size: 1.08rem;
    line-height: 1.7rem;
    color: #e3e6ee;
    margin-bottom: 1rem;
    transition: color 0.2s;
}
.about-me-container p:hover {
    color: #b366f6;
    filter: contrast(20);
}
